.cardButton {
  width: 100%;
  height: 45px;
  border: none;
  background: #428dfe;
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  margin-top: 15px;
  visibility: hidden;
  cursor: pointer;

  &:hover {
    background: #3978d8;
  }

  &:active,
  &:focus {
    outline: 0;
  }
}

.cardButtonShow {
  visibility: visible;
}

@media screen and (max-width: 768px) {
  .cardButtonShow {
    visibility: visible;
    cursor: auto;
  }
}
