.wrapper {
  h2 {
    margin: 45px 0 45px 0px;
    text-transform: uppercase;
    font-size: 26px;
    color: #252525;
  }
  hr {
    border: none;
    color: #dbdbdb;
    background-color: #dbdbdb;
    height: 1px;
    margin: 35px 0;
  }
}

input[type='text'],
input[type='number'] {
  width: 100%;
  margin-bottom: 10px;
}
.inputForMobile {
  width: 100%;
  font-size: 26px;
  text-transform: uppercase;
  color: #131313;
}

.textForMobile {
  margin-top: 50px;
  width: 100%;
  height: 30px;
}

.mainPhotoClient {
  width: 50%;
  max-height: 65vh;
  overflow: hidden;
}

.productInfo {
  display: flex;
  margin: 60px 0 15px;

  .mainPhoto {
    position: relative;
  }

  .mainPhoto .deleteIcon {
    width: 23px;
    position: absolute;
    right: 0;
  }

  .mainPhoto img {
    width: 100%;
  }

  .mainPhoto .changePhoto {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(196, 196, 196, 0.5);
    border: none;
    height: 70px;
  }

  .description {
    margin-left: 15px;
    max-width: calc(50% - 30px);

    .buyButtonContainer {
      width: 220px;
    }
  }

  .cardTitle {
    font-size: 26px;
    text-transform: uppercase;
    color: #131313;
  }

  .price {
    font-size: 30px;
    font-weight: bold;
    line-height: 36px;
    margin: 10px 0;
  }

  .buyButton {
    min-width: 220px;
    padding: 12px;
    background-color: #428dfe;
    border: none;
    color: #fff;
  }

  .descriptionText {
    margin: 15px 0 0;
    font-size: 18px;
    color: #252525;
    text-align: justify;
  }
}

.imgWrapper {
  position: relative;
  margin: 5px;

  .deleteIcon {
    width: 23px;
    position: absolute;
    right: 0;
  }

  .changePhoto {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(196, 196, 196, 0.5);
    border: none;
    height: 70px;
  }

  .changePhotoBig {
    width: 50%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(196, 196, 196, 0.5);
    border: none;
    height: 70px;
  }

  .deleteIconBig {
    width: 23px;
    position: absolute;
    right: 50%;
  }
}
.addPhotoDiv {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 115%;
}
.addPhotoButton {
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  background-color: rgba(120, 120, 120, 0.7);
  color: #fff;
  text-transform: uppercase;
  border: none;
  height: 100%;
}
.otherPhotos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  .bigPhoto {
    grid-column: 1 / span 2;
    grid-row: 1 / span 2;
  }
  img {
    width: 100%;
  }
}

.characteristicsImg {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);

  img {
    width: 100%;
  }
}

// .bigPhoto {
//   grid-column: 1 / span 2;
//   grid-row: 1 / span 2;
// }

@media screen and (max-width: 429px) {
  .productInfo {
    flex-direction: column;
  }
  .mainPhoto {
    width: 100%;
  }
  .productInfo div img {
    width: 80vw;
    height: auto;
  }
}
