.title {
  text-transform: uppercase;
  margin-bottom: 30px;
}

.videoChangingDiv {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  margin-bottom: 15px;
  .input {
    width: 70%;
    height: 2em;
    font-size: 20px;
    margin-bottom: 0;
  }
  .addButton {
    background: #48b946;
    height: 45px;
    width: 240px;
    font-size: 16px;
    color: #ffffff;
    text-transform: uppercase;
    border: none;
    cursor: pointer;

    &:hover {
      background: #2e762d;
    }
  }
}

.video {
  display: flex;
  justify-content: center;
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 0px;
  height: 0;
  overflow: hidden;
}
iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.videoWrapper {
  .deleteIcon {
    width: 69px;
    position: absolute;
    right: 5px;
    top: 5px;
    padding: 5px;
    cursor: pointer;
    border: 1px solid rgb(158, 31, 31);
    background-color: rgb(255, 255, 255);
  }
  .deleteIcon:hover {
    background-color: rgb(218, 218, 218);
  }
}

@media screen and (max-width: 420px) {
  .video {
    display: flex;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 15px 0 0;
    overflow: visible;
  }
  iframe {
    position: relative;
    height: 65vh;
    width: 100%;
    padding: 0;
    margin: 0;
  }
  .videoContainerWrapper h2 {
    margin-bottom: 0;
    text-align: center;
  }
}
