.wrapper {
  padding: 75px 0 45px;

  .title {
    text-transform: uppercase;
    font-size: 26px;
    padding-bottom: 20px;
  }

  .cardAlbum {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 263px));
    grid-gap: 30px 19px;
  }
}

@media screen and (max-width: 1024px) {
  .wrapper {
    .cardAlbum {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

@media screen and (max-width: 568px) {
  .wrapper {
    .cardAlbum {
      grid-template-columns: repeat(2, minmax(100px, 1fr));
    }
  }
}
