.wrapper {
  padding: 35px 0 30px;

  .topBlock {
    display: flex;
    justify-content: space-between;

    .titleBlock {
      display: flex;
      justify-content: space-between;

      .categoryTitle {
        margin-right: 20px;
      }
      .categoryTitle:last-child {
        margin-right: 0;
      }
    }
  }

  .productAlbum {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 263px));
    grid-gap: 30px 19px;
    margin-top: 20px;
  }

  .addButton {
    width: 240px;
    height: 45px;
    font-size: 16px;
    color: #ffffff;
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    background: #48b946;

    &:hover {
      background: #2e762d;
    }
  }

  .addButtonNoProducts {
    display: flex;
    justify-content: flex-end;
  }
}

@media screen and (max-width: 1024px) {
  .wrapper {
    .productAlbum {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

@media screen and (max-width: 568px) {
  .wrapper {
    .productAlbum {
      grid-template-columns: repeat(2, minmax(100px, 1fr));
    }
    .topBlock {
      flex-direction: column;
      .titleBlock {
        justify-content: flex-start;
        display: none;
      }
    }
  }
}
