hr {
  border: none; /* Убираем границу для браузера Firefox */
  color: #dbdbdb; /* Цвет линии для остальных браузеров */
  background-color: #dbdbdb; /* Цвет линии для браузера Firefox и Opera */
  height: 1px; /* Толщина линии */
  margin: 45px 0;
}

.reviewsWrapper {
  width: 80%;
  margin: 50px auto 10px;
}

.info {
  display: flex;
  margin: 10px 0;
  justify-content: space-between;
}

.userInfo {
  width: 100%;
  font-size: 20px;
  display: flex;
  flex-direction: column;

  p {
    margin-right: 10px;
  }
}

.nameBlock {
  width: 100%;
  display: flex;
  justify-content: space-between;

  .date {
    color: rgba(19, 19, 19, 0.5);
  }
}

.text {
  font-size: 16px;
  line-height: 1.3em;
  color: rgba(19, 19, 19, 0.7);
  margin-bottom: 10px;
}

.imgContainer {
  img {
    max-width: 300px;
    max-height: 300px;
  }
}

.reviews {
  position: relative;
}
.deleteIcon {
  width: 27px;
  position: absolute;
  right: -50px;
  top: 0;
}
.showMoreButton {
  width: 176px;
  height: 41px;
  display: block;
  margin: 25px auto;
  font-size: 16px;
  color: #252525;
  background: #fff;
  border: 2px solid #252525;
  cursor: pointer;
  text-align: center;
  &:hover {
    background: #252525;
    color: #fff;
  }
  &:active,
  &:focus {
    outline: 0;
  }
}
@media screen and (max-width: 767px) {
  .reviewsWrapper {
    width: 100%;
    text-align: justify;
    margin-top: 30px;
  }

  .info {
    margin: 20px 0 10px;
    color: rgba(19, 19, 19, 0.7);
    font-size: 16px;
  }
  .userInfo {
    font-size: 20px;
    color: #131313;
  }
  .imgContainer {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
