.card {
  position: relative;

  .mainPhotoDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 300px;
    overflow: hidden;
    margin-bottom: 15px;
    background-position: center;
    background-size: cover;
    position: relative;
  }
  // .img,
  // .noImgDiv {
  //   width: 100%;
  //   height: auto;
  //   box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
  // }

  .iconBlock {
    width: 70px;
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 10px;
    right: 10px;

    .editIcon,
    .deleteIcon {
      cursor: pointer;

      &:hover {
        transform: scale(1.1, 1.1);
      }
    }

    .deleteIcon {
      margin-bottom: 4px;
    }
  }

  .cardTextBlock {
    color: #131313;

    .cardName {
      text-transform: uppercase;
      font-size: 16px;
    }

    .cardText {
      font-size: 12px;
    }

    .cardPrice {
      font-size: 20px;

      .cardPriceBold {
        font-weight: bold;
      }
    }

    .cardButton {
      width: 100%;
      height: 45px;
      border: none;
      background: #428dfe;
      color: #fff;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 16px;
      margin-top: 15px;
      visibility: hidden;
      cursor: pointer;

      &:hover {
        background: #3978d8;
      }

      &:active,
      &:focus {
        outline: 0;
      }
    }

    .cardButtonShow {
      visibility: visible;
    }
  }

  .imgShadow {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  }
}

@media screen and (max-width: 768px) {
  .card {
    .img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 568px) {
  .cardTextBlock {
    text-align: center;
  }
  .card {
    .mainPhotoDiv {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 25vh;
      margin-bottom: 5px;
    }
    .img,
    .noImgDiv {
      width: 100%;
      height: auto;
    }
  }
}
