.wrapper {
  position: relative;

  .mainBlock {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #dbdbdb;
    padding: 20px 0;

    .categoryBlock {
      display: flex;
      align-items: center;

      .subCategoryLink {
        color: #7c7c7c;
        margin-left: 15px;

        a {
          color: #7c7c7c;
        }

        span {
          margin-left: 5px;
        }
      }
    }

    .subCategoryBlock {
      .categoryHeading {
        text-transform: uppercase;
        font-size: 20px;
      }

      .subCategoryNonEdit {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
      }
    }
  }

  img {
    margin-left: 5px;

    &:hover {
      cursor: pointer;
      transform: scale(1.1, 1.1);
    }
  }

  .editInput {
    width: 180px;
    outline: 0;
    padding: 10px 20px;
    margin: 5px 0;
  }

  .categorySubmit {
    width: 80px;
    height: 42px;
    background: #3dd15e;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    color: #fff;

    &:hover {
      background: #2e762d;
    }
  }

  .editName {
    min-width: 250px;
    padding: 10px 0;
    border: 1px solid black;
    text-align: center;
    margin: 0 5px 5px 0;
  }
}

.categoryForm {
  div {
    display: flex;
    flex-direction: column;
  }
}
