.modal {
  position: fixed;
  z-index: 200;
  background-color: white;
  box-sizing: border-box;
  min-height: 360px;
  top: 32%;
  left: 30%;
  width: 40%;
  box-shadow: 0 6px 8px -2px rgba(0, 0, 0, 0.5);
  display: none;

  .modalMain {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 290px;
    padding: 30px;
    text-align: center;
    font-size: 24px;
    color: #575757;

    .modalText {
      line-height: 1.5;
      margin-top: 30px;
    }
  }
}

.open {
  display: block;
}

.close {
  display: none;
}
