.wrapper {
  padding: 50px 0;
  min-height: 500px;

  .adminTitle {
    width: 100%;
    border-bottom: 1px solid #6c6c6c;
    font-size: 26px;
    font-weight: 600;
    text-transform: uppercase;
    color: #252525;
  }

  .adminMain {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;

    .adminMainLeft {
      width: 25%;

      ul,
      li {
        list-style: none;
        font-size: 20px;
        line-height: 2;
        margin-top: 0;
        padding-top: 0;
      }

      .listItem {
        font-weight: 600;
        color: #252525;

        .listSubItem {
          font-weight: 400;

          sup {
            font-size: 12px;
            padding: 3px 5px;
            background: #ff6e6e;
            color: #fff;
            border-radius: 50%;
          }

          a {
            text-decoration: none;
            color: #434343;

            &:hover {
              color: #000;
              font-weight: 600;
            }
          }
        }
      }
    }

    .adminMainRight {
      width: 75%;

      .subTitle {
        width: 100%;
        font-size: 20px;
        color: #252525;
        border-bottom: 1px solid #dbdbdb;
        margin-bottom: 30px;
      }

      .addButton {
        width: auto;
        background: none;
        border: none;
        cursor: pointer;
        color: #1e78ff;
        padding: 0;

        &:hover {
          font-weight: 700;
        }
      }
    }
  }

  .editInput {
    width: 180px;
    outline: 0;
    padding: 10px 20px;
    margin: 10px 0;
  }

  .categorySubmit {
    width: 80px;
    height: 42px;
    background: #3dd15e;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    color: #fff;

    &:hover {
      background: #2e762d;
    }
  }
}

.categoryForm {
  div {
    display: flex;
    flex-direction: column;
  }
}
