.title {
  text-transform: uppercase;
}

.characteristicsDiv {
  position: relative;
  display: flex;
  justify-content: center;
  .deleteIcon {
    width: 23px;
    position: absolute;
    right: 0;
  }

  img {
    width: 100%;
  }
}

.titleWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 429px) {
  .characteristicsDiv {
    img {
      width: 100%;
      margin: 15px 0;
    }
  }
  .characteristicsWrapper h2 {
    text-align: center;
  }
}
