.commentForm {
  width: 65%;
  margin: 5rem auto 5rem;
  color: #454545;
  font-size: 16px;
  font-weight: 300;
  line-height: 23px;

  h2 {
    margin: 45px 0 45px 0px;
    text-transform: uppercase;
    font-size: 26px;
    color: #252525;
  }

  form {
    display: flex;
    flex-direction: column;
  }

  input {
    color: #252525;
    background-color: #ffffff;
    border: 1px solid #cccccc;
    outline: none;
    height: 3rem;
    width: 100%;
    font-size: 17px;
    margin: 10px auto;

    &::placeholder {
      opacity: 0.4;
    }
  }
  .deleteIcon {
    width: 12px;
    height: 12px;
    margin-left: 10px;
    &:hover {
      cursor: pointer;
    }
  }
}

.reviewInfo {
  display: flex;
  justify-content: space-between;

  input:not(:first-child) {
    margin-left: 30px;
  }

  input {
    padding: 11px;
    flex-grow: 1;
    width: 100%;
  }
}

.textReviews {
  input {
    padding: 35px 10px;
  }
}

.buttonContainer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 30px;

  button {
    background-color: #fff;
    border: 2px solid #252525;
    color: #252525;
    line-height: 23px;
    min-width: 220px;
    padding: 10px;
    text-transform: uppercase;
    cursor: pointer;

    &:hover {
      background: #252525;
      color: #fff;
    }
  }
}

@media screen and (max-width: 767px) {
  .commentForm {
    width: 100%;
    margin: 30px 0 0;

    h2 {
      text-align: center;
      font-size: 20px;
      margin: 0px;
    }
  }
}

@media screen and (max-width: 568px) {
  .buttonContainer {
    button {
      margin: 10px 0 50px;
    }
  }
}
