.wrapper {
  hr {
    border: none;
    color: #dbdbdb;
    background-color: #dbdbdb;
    height: 1px;
    margin: 35px 0;
  }
}

.bigPhoto {
  grid-column: 1 / span 2;
  grid-row: 1 / span 2;
}

.descriptionTextMobile {
  font-size: 20px;
  color: #131313;
  text-align: justify;
  margin-top: 15px;
}

@media screen and (max-width: 767px) {
  .wrapper {
    hr {
      margin: 20px 0 60px;
    }
  }

  .reviewBtn {
    width: 100%;
    padding: 12px;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin-bottom: 60px;
    background-color: #fff;
    border: 2px solid #252525;
    box-sizing: border-box;
  }

  .reviewBtn:hover {
    opacity: 0.5;
  }
}
