.infoBlock {
  margin-bottom: 100px;
  width: 100%;

  .productTitle {
    color: #434343;
    font-weight: 600;
  }

  .feedbackRate {
    width: 100%;
    margin: 20px 0 15px 0;

    .userInfo {
      width: 100%;
      display: flex;
      flex-direction: column;

      .userInfoUp {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;

        .userInfoLeft {
          display: flex;
        }
      }

      .feedbackText {
        margin: 15px 0;
        color: rgba(19, 19, 19, 0.7);
      }

      .imgContainer {
        img {
          max-width: 300px;
          max-height: 300px;
        }
      }
    }

    .username {
      font-size: 20px;
      color: #131313;
      margin-right: 10px;
    }

    .feedbackDate {
      color: rgba(19, 19, 19, 0.5);
    }
  }

  .usersRate {
    font-weight: 700;
    font-size: 20px;
  }

  .buttonBlock {
    width: 80%;
    height: 80px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-top: 1px solid #dbdbdb;
    margin-top: 5px;

    .buttonSubBlock {
      width: 440px;
      display: flex;
      justify-content: space-between;

      button {
        width: 260px;
        height: 45px;
        border: none;
        cursor: pointer;
        background: #3dd15e;
        text-transform: uppercase;
        color: #fff;

        &:hover {
          background: #2e762d;
        }
      }

      .greyBtn {
        background: #bababa;

        &:hover {
          background: #898989;
        }
      }

      .deleteButton {
        width: 140px;
        background: #ff6e6e;

        &:hover {
          background: rgb(145, 60, 60);
        }
      }
    }
  }
}
