.appHeader {
  //position: sticky;
  position: relative;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0 6px 8px -2px rgba(0, 0, 0, 0.2);
  top: 0;
  z-index: 19;
  min-height: 3rem;
}

