.paginationBlock {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  font-weight: bold;

  span {
    padding: 10px 16px;
    color: #000;
    background: #fff;
    border: 2px solid #000;
    margin: 0 5px;

    &:hover {
      cursor: default;
    }
  }

  .selected {
    background-color: #000;
    color: #fff;
  }
}
