.mainPart {
  .nameUpper {
    margin: 15px 0 10px 0px;
    text-transform: uppercase;
    font-size: 22px;
    color: #252525;
    text-align: center;
    font-weight: normal;
  }

  .productInfo {
    display: flex;
    flex-direction: column;

    img {
      width: 100%;
      height: auto;
    }
  }

  .cardTitle {
    font-size: 22px;
    text-transform: uppercase;
    color: #131313;
    margin-top: 40px;
  }

  .cardTitleLong {
    font-size: 20px;
    text-transform: uppercase;
    color: #131313;
    margin-top: 10px;
  }

  .stars {
    margin-bottom: 15px;
  }

  .price {
    font-size: 30px;
    font-weight: bold;
    line-height: 36px;
    margin-bottom: 10px;
  }

  .buyButton {
    width: 100%;
    padding: 12px;
    background-color: #428dfe;
    border: none;
    color: #fff;
  }
}

.otherPhotos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);

  img {
    width: 100%;
  }
}

.characteristicsImg {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);

  img {
    width: 100%;
  }
}

.bigPhoto {
  grid-column: 1 / span 2;
  grid-row: 1 / span 2;
}

.reviewsWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  button {
    min-width: 226px;
    padding: 12px;
    background-color: #428dfe;
    border: none;
    color: #fff;
  }
}
