.feedbackTitle {
  text-transform: uppercase;
}

.reviewsWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  .buyButtonContainer {
    width: 220px;
  }
}

@media screen and (max-width: 568px) {
  .reviewsWrapper {
    .buyButtonContainer {
      width: 100%;
    }
  }
  .feedbackTitle {
    text-align: center;
  }
}
