.description {
  .descriptionDiv {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1em;
  }

  textarea {
    width: 100%;
    height: 40vh;
    font-size: 20px;
    text-align: justify;
    line-height: 30px;
    padding: 15px 30px;
  }

  .addButton {
    background: #48b946;
    height: 45px;
    width: 240px;
    font-size: 16px;
    color: #ffffff;
    text-transform: uppercase;
    border: none;
    cursor: pointer;

    &:hover {
      background: #2e762d;
    }
  }

  span {
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 600;
  }
}
