.wrapper {
  .nonEditingBlock {
    .textBlock {
      h3 {
        width: 300px;
        padding: 10px 0;
        border: 1px solid black;
        text-align: center;
        margin-bottom: 15px;
      }
    }

    .buttonBlock {
      width: 300px;
      margin-top: 40px;
      display: flex;
      justify-content: flex-end;

      .cancelButton {
        background: #ff6e6e;

        &:hover {
          background: rgb(145, 60, 60);
        }
      }
    }
  }

  .editingBlock {
    width: 85%;
    display: flex;
    justify-content: space-between;
    color: #434343;

    .editingForm {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      div {
        margin-bottom: 15px;

        input {
          width: 300px;
          outline: 0;
          padding: 10px 20px;
          margin-right: 10px;
        }

        img {
          &:hover {
            cursor: pointer;
            transform: scale(1.1, 1.1);
          }
        }
      }
    }
  }

  .button {
    width: 145px;
    height: 45px;
    border: none;
    cursor: pointer;
    background: #3dd15e;
    text-transform: uppercase;
    color: #fff;

    &:hover {
      background: #2e762d;
    }
  }

  .regNumber {
    display: flex;
    flex-direction: column;
  }

  .filesInputBlock {
    width: 300px;
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;

    div {
      margin: 20px 0 20px;
    }
  }
}
