.filterBlock {
  display: flex;
  justify-content: flex-end;

  .filterLabel {
    padding-right: 15px;
    color: #252525;
    transform: translateY(12%);
  }

  .filterSelect {
    width: 213px;
    height: 28px;
    padding-left: 12px;

    &:focus,
    &:active {
      outline: none;
    }
  }
}

@media screen and (max-width: 568px) {
  .filterBlock {
    justify-content: center;
  }
  .filterBlock {
    justify-content: center;

    .filterLabel {
      display: none;
    }

    .filterSelect {
      width: 260px;
      height: 45px;
    }
  }
}
