.wrapper {
  height: 758px;
  background-position: center;
  background-size: cover;
  position: relative;
}

.content {
  position: relative;
  height: 100%;
  &__inner {
    // https://css-tricks.com/centering-css-complete-guide/
    position: absolute;
    top: 40%;
    height: 100px;
    margin-top: -50px;
    width: 50%;
  }
}

.text {
  text-transform: uppercase;
  font-family: MyriadPro-Semibold, sans-serif;
  text-align: center;
  font-weight: bold;
}

.title {
  font-size: 52px;
}

.subtitle {
  font-size: 36px;
  margin-top: 45px;
  margin-left: 80px;
  margin-right: 80px;
}

.filterButton {
  // visibility: hidden;
  position: absolute;
  left: calc(100vw / 2 - 88px);
  width: 176px;
  height: 41px;
  display: none;
  margin: 25px auto;
  font-size: 20px;
  justify-content: space-evenly;
  align-items: center;
  color: #252525;
  border: none;
  background: rgba(250, 249, 249, 0.2);
  // border: 2px solid #fff;
  cursor: pointer;
  text-align: center;
  &:hover {
    background: none;
    color: #fff;
  }
  &:active,
  &:focus {
    outline: 0;
  }
  // > img {
  //   color: black;
  // }
}

.editBtn {
  width: 100%;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0);
  color: #252525;
  font-size: 26px;
  border: none;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  cursor: pointer;
  &:hover {
    opacity: 0.5;
  }
}
.editBtn::before {
  content: url(~assets/icons/pencil.png);
  position: relative;
  right: 15px;
  top: 5px;
  width: 25px;
}

.form {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 30px;
  right: 50%;
  width: 50%;
  font-size: 16px;
  background-color: #fff;
}

.form input[type='text'] {
  font-size: 30px;
  height: 40px;
  width: 100%;
  padding: 6px 12px;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #000;
  box-sizing: border-box;
  text-align: center;
}
.form label {
  font-size: 20;
  text-align: start;
  margin-top: 10px;
}
.form textarea {
  font-size: 25px;
  height: 70px;
  width: 100%;
  padding: 6px 0;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #000;
  box-sizing: border-box;
  text-align: center;
}
.form input[type='submit'] {
  margin: 20px auto;
  height: 40px;
  width: 220px;
  background: rgba(25, 25, 25, 0.8);
  color: #fff;
  padding: 14px 30px;
  border: none;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  cursor: pointer;
  &:hover {
    background: rgba(25, 25, 25, 0.6);
  }
}
.previewBtn {
  margin-top: 13px;
  background: #fff;
  color: #252525;
  padding: 14px 30px;
  border: none;
  letter-spacing: 0.05em;
  font-weight: bold;
  text-transform: uppercase;
}
.addUpdateBtnDiv {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.updateBtn {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 70px;
  color: #252525;
  cursor: pointer;
  font-weight: 700;
  border: none;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(25, 25, 25, 0.8);
}
.updateBtn:hover {
  background-color: rgba(25, 25, 25, 0.6);
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 42px;
  }

  .subtitle {
    font-size: 26px;
    margin-top: 25px;
    margin-left: 0;
    margin-right: 0;
  }
  .filterButton {
    display: flex;
  }
}

@media screen and (max-width: 420px) {
  .title {
    font-size: 30px;
  }

  .subtitle {
    font-size: 18px;
    margin-top: 20px;
    margin-left: 0;
    margin-right: 0;
  }
  .filterButton {
    display: flex;
  }
}

@media screen and (max-width: 1024px) {
  .filterButton {
    display: flex;
  }
}
