.cartItem {
  display: flex;
  position: relative;
  margin: 1.5em 0 1.5em;
  font-size: 20px;

  .itemImage {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
    height: 30vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .infoPart {
    flex: 4;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1.875em;
    border-bottom: 1px solid #dbdbdb;

    .itemName {
      flex: 2;
      display: flex;
      justify-content: flex-start;
      text-transform: uppercase;
    }

    .itemCount {
      display: flex;
      justify-content: center;
      align-items: center;

      flex: 1.2;

      button {
        width: 30px;
        height: 30px;
        background: #f3f3f3;
        border: 1px solid #dcdcdc;
        box-sizing: border-box;
        cursor: pointer;
      }
      button:hover {
        background: #b5b3b3;
      }

      span {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        width: 30px;
        height: 30px;
        border: 1px solid #dcdcdc;
        box-sizing: border-box;
        box-shadow: inset 0px 0px 5px rgba(0, 0, 0, 0.1);
      }
    }

    .itemPrice {
      flex: 1;
      text-align: right;

      span {
        font-weight: 600;
      }
    }
  }

  .deleteButton {
    position: absolute;
    right: 0px;
    width: 15px;
    height: 15px;
    background-image: url('~assets/icons/delete.png');
    background-color: #ffffff;
    background-repeat: no-repeat;
    cursor: pointer;
    border: none;
    background-size: cover;
  }
}

@media screen and (max-width: 767px) {
  .cartItem {
    border-bottom: 1px solid #dbdbdb;
    padding-bottom: 0.5em;

    .itemImage {
      flex: 4;
    }

    .infoPart {
      flex-direction: column;
      margin-left: 0;
      border-bottom: none;
      margin-top: 3em;
      margin-left: 0.5em;

      .itemName {
        flex: 1.5;
        justify-content: flex-start;
        width: 100%;
      }
      .itemCount {
        flex: 1;

        justify-content: flex-start;
        width: 100%;
        order: 3;

        button {
          width: 40px;
          height: 40px;
        }

        span {
          width: 40px;
          height: 40px;
        }
      }
      .itemPrice {
        flex: 1;
        text-align: left;

        width: 100%;
        order: 2;
      }
    }
  }
}
