.titleWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  h2 {
    text-transform: uppercase;
    margin-bottom: 30px;
  }

  button {
    width: 240px;
    height: 45px;
    background-color: #48b946;
    color: white;
    border: none;
  }
}

.slideContainer {
  img {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .titleWrapper {
    justify-content: center;
  }
  .clientsPhotosBlock h2 {
    margin-bottom: 15px;
    text-align: center;
  }
}
