.mainPhoto {
  position: relative;
  overflow: hidden;
  width: 100%;
  min-height: 100px;
}

.mainPhoto .changePhoto {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(196, 196, 196, 0.5);
  border: none;
  height: 70px;
}

.deleteIcon {
  position: absolute;
  right: 20px;
  top: 0;
  background-image: url('~assets/icons/delete.png');
  width: 25px;
  height: 25px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  background-color: rgba(196, 196, 196, 0);
  cursor: pointer;
}

.photoItem {
  width: 100%;
}
