.otherPhotos {
  display: grid;
  height: 65vh;
  // grid-gap: 30px 19px;

  .bigPhoto,
  .smallPhoto {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    .photoBgcWrapper {
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      position: relative;
    }
    :hover {
      cursor: pointer;
    }
    .checked {
      border: 1px solid red;
      box-sizing: border-box;
