.modal {
  position: fixed;
  z-index: 200;
  background-color: white;
  box-sizing: border-box;
  min-height: 50vh;
  top: 5%;
  left: 30%;
  width: 40%;
  display: none;

  .closeButton {
    background-color: #fff;
    color: red;
    width: 40px;
    height: 40px;
    border: 1px solid red;
    margin: 10px 10px 0 0;
    position: absolute;
    right: 0;
  }

  .modalTop {
    height: 70px;
    background: #252525;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    font-size: 28px;
  }

  .modalMain {
    display: flex;
    flex-direction: column;
    padding: 30px;
    font-size: 24px;
    color: #575757;

    .headerTitle {
      text-transform: uppercase;
      text-align: center;
      margin-bottom: 30px;
    }
    .modalText {
      line-height: 1.5;
    }

    input[type='text'],
    input[type='number'] {
      // width: 100%;
      margin-bottom: 20px;
    }
    .description {
      margin-left: 15px;
      max-width: calc(100% - 30px);
    }

    .descriptionText {
      margin: 15px 0 0;
      font-size: 18px;
      color: #252525;
      // text-align: justify;
    }

    .cardTitle {
      font-size: 26px;
      text-transform: uppercase;
      color: #131313;
    }

    .category {
      margin-bottom: 20px;
    }

    select {
      width: 100%;
      height: 1.5em;
    }

    .saveBtn {
      display: flex;
      justify-content: center;
    }
    .buyButton {
      min-width: 220px;
      padding: 12px;
      background-color: #428dfe;
      border: none;
      color: #fff;
      margin: 20px auto;
    }
  }
}

.open {
  display: block;
}

.close {
  display: none;
}

@media screen and (max-width: 1024px) {
  .modal {
    left: 25%;
    width: 50%;
  }
}

@media screen and (max-width: 768px) {
  .modal {
    .modalMain {
      padding: 20px;

      .buttonBlock {
        flex-direction: column;

        a {
          width: 100%;
        }

        button {
          width: 100%;
        }

        .continueButton {
          margin-bottom: 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 568px) {
  .modal {
    width: 94%;
    left: 3%;

    .modalTop {
      font-size: 18px;
    }

    .modalMain {
      font-size: 20px;
    }
  }
}
